@font-face {
  font-family: 'Rufina-Bold';
  src:url(/static/media/Rufina-Bold.bdf05f51.ttf);
}
 
body, button, input, select, textarea {
    font-size:12px;
    line-height:1.5;
    font-family: 微软雅黑, 宋体, tahoma, arial;
}
body, h1, h2, h3, h4, h5, h6, hr, p, blockquote, dl, dt, dd, ul, ol, li, pre, form, fieldset, legend, button, input, textarea, th, td, form {
    margin: 0;
    padding: 0;
}


.head{
    background-color: #2C2F33;
    height: 52px;
    overflow: hidden;
}
.head_main{
	width: 1000px;
    margin: 0 auto;
    overflow: hidden;
    font-family: PingFangSC-Regular;
}
.logo{
	text-indent: -9999em;
    display: block;
    width: 125px;
    height: 20px;
    margin-top: 16px;
    float: left;
    text-align:right;
}
.divider,.website{
	float: left;
    color: white;
    font-style: normal;
    font-size: 20px;
    line-height: 52px;
    margin: 0 10px;
    opacity: 0.8;
}
.website{
    margin:0;
    font-size: 14px;
    line-height: 54px;
}
.head_menu{
    list-style: none;
}
.head_menu li{
    list-style: none;
    display: inline;
    position: relative;
    z-index: 2;
}
.head .head_menu li a,.login a {
    text-decoration: none;
    font-size: 14px;
    color: #fff;
    opacity: 0.8;
    filter: alpha(opacity=80);
    line-height: 52px;
    padding: 10px 15px;
    margin-left: 4px;
}
.current-cat a,.head-menu a:hover {
    color: #F37327;
    opacity: 1;
    filter: alpha(opacity=100);
    font-family: PingFangSC-Regular;
}
.login{
    width: 340px;
    float: right;
    line-height: 52px;
    color: #fff;
    text-align: right;
    opacity: 0.8
}
.login a{
    padding:0 4px;
    margin:0;
    font-size:12px;
}

.main{
    width: 100%;
    height: 478px;
    overflow: hidden;
    font-family: PingFangSC-Regular;
    position:relative;
}
.main_img{
	vertical-align: middle;
	width:100%;
	height:478px;
}
.main_pop{
	position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(31,56,88,0.40);
}
.main_content{
	width: 1000px;
    height:100%;
    position: absolute;
    margin-left: -500px;
    top: 0;
    left: 50%;
}
.float_mid{
    position: absolute;
    left:50%;
	top: 50%;
    margin-left: -245px;
    margin-top: -145px;
}
.en_world{
	font-size: 54px;
    color: #fff;
    line-height: 56px;
    font-family: Rufina-Bold;
}
.zh_world{
	font-size: 34px;
    color: #fff;
    margin-bottom: 38px;
    line-height: 48px;
} 
.search_box{
	width: 490px;
    height: 60px;
    position: relative;
    border: 0px;
    background: none;
    margin: 0 auto;
}
.search_topic{
    position: absolute;
    left:15px;
    color: #A1A1A1;
    z-index: 90;
    font-size: 14px;
    top: 50%;
    margin-top: -10px;
    line-height: 22px;
    display: block;
    width: 300px;
}
.search_wrap{
	width: 100%;
    height: 100%;
    background: #fff;
    position: absolute;
    opacity: 0.1;
    filter: alpha(opacity=10);
}
.search_text{
    width: 368px;
    height: 40px;
    margin-top: 6px;
    margin-left: 10px;
    box-sizing: border-box;
    border-radius: 3px;
    padding: 10px 0 6px;
    text-indent: 10px;
    float: left;
    border: 0;
    font-size: 14px;
    position: relative;
    top: 4px;
    outline: none;
}
.search_btn{
    color: #fff;
    background: #F37327;
    font-size: 14px;
    font-family: PingFangSC-Regular;
    width: 92px;
    height: 40px;
    border-radius: 3px;
    margin-top: 10px;
    margin-left: 10px;
    position: relative;
    z-index: 2;
    border:0;
    text-align: center;
}
.hot_text{
	padding:10px 0;
	color:rgba(255,255,255,0.80);
	text-anchor: center;
}
.hot_text a{
    text-decoration: none;
    color: rgba(255,255,255,0.80);
    padding-right: 10px;
    cursor: pointer;
}
.searth_key{
	color:rgba(255,255,255,0.80);
	padding-right:10px;
	text-decoration: none;
	cursor:pointer;
}
    ul{
        list-style: none;
        margin: 0;
        padding: 0;
    }
    #scrollUpBox {
        width:100%;
        height:100%;
        overflow: hidden;
    }
    #content li{
        padding: 0 14px;
        line-height: 50px;
    }
    .content li a{
        float: left;
        color: #3C99D8;
        cursor: pointer;
        font-size: 14px;
        width: 400px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        text-decoration: none
    }
    .time{
        font-style: normal;
        float: right;
        width: 70px;
        text-align: right;
        font-size: 14px;
        color: rgba(31,56,88,0.60);
    }
    .city{
        float: right;
        font-style: normal;
        font-size: 14px;
        color: rgba(0,0,0,0.60);
        margin-right: 70px;
        width: 202px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
    em{
        font-style: italic;
    }

.content{
	height: 364px;
    width: 100%;
    overflow: hidden;
}
.new_position{
	width: 1180px;
    height: 300px;
    margin: 32px auto;
}
.new_list{
	width: 850px;
    height: 300px;
    background: #FFFFFF;
    box-shadow: 0 0 15px 0 rgba(31,56,88,0.08);
    border-radius: 3px;
    float: left;
    overflow: hidden;
    position: relative;
}
.more_position{
    height: 49px;
    border-bottom: 1px solid rgba(31,56,88,0.06);
    line-height: 49px;
    padding: 0 14px;
    /* opacity: 0.8; */
    background: white;
    font-size: 14px;
    color: #000000;
    position: relative;
    z-index: 2;
}
.more_position a{
    float: right;
    font-size: 14px;
    color: rgba(31,56,88,0.60);
    cursor: pointer;
    text-decoration: none;
}
.pic_wrap{
    width: 310px;
    float: right;
}
.show_pic{
	text-align: center;
    line-height: 140px;
    display: block;
    cursor: pointer;
    height: 140px;
    background: #FFFFFF;
    box-shadow: 0 0 15px 0 rgba(31,56,88,0.08);
    border-radius: 3px;
}
.pic_1 {
    margin-bottom: 20px;
}
 
.pic_1 img{
	width:179px;
	vertical-align: middle;
}
.pic_2 img{
	width: 260px;
    vertical-align: middle;
}
.footer{
	width: 100%;
    height: 44px;
    background: #2C2F33;
    text-align:center;
}

